  gap: 10px;
  padding: 8px;
  cursor: pointer;
}

.suggestion-item:hover {
  background-color: #f5f5f5;
  border-radius: 6px;
}

.suggestion-title {
  font-weight: 600;
  font-size: 14px;
}

.suggestion-desc {
  font-size: 12px;
  color: #666;
}

/* guest selector */
.dropdownBox {
  position: absolute;
  top: 110%;
  left: 0;
  z-index: 10;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
  padding: 20px;
  width: 330px;
}

.roomLabel {
  font-weight: 600;
  margin-bottom: 12px;
}

.counterRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.counter {
  display: flex;
  align-items: center;
  gap: 10px;
}

.counter button {
  background-color: #f0f0f0;
  border: none;
  padding: 6px 10px;
  font-size: 18px;
  border-radius: 6px;
  cursor: pointer;
}

.ageSelectors {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 10px;
}

.ageSelectors select {
  width: 80px;
  padding: 4px;
  border-radius: 6px;
  border: 1px solid #ccc;
}

.addRoomBtn {
  background: none;
  border: none;
  color: #0560FB;
  margin-top: 16px;
  cursor: pointer;
  font-weight: 500;
}

.doneBtn {
  background-color: #0560FB;
  color: white;
  padding: 10px 16px;
  margin-top: 20px;
  width: 100%;
  border: none;
  border-radius: 10px;
  font-weight: 600;
  font-size: 16px;
  cursor: pointer;
}

.explore-rooms-guests {
  border: 1px solid #DCDCDC;
  height: 48px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding: 11px 12px;
  gap: 8px;
  cursor: pointer;
  width: 330px;
  position: relative;
}

.room-guest-wrapper {
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
}

.room-guest-input {
  border: none;
  outline: none;
  font-size: 14px;
  background-color: transparent;
  width: 100%;
  color: #000;
  cursor: pointer;
}

.roomBlock {
  padding-bottom: 20px;
  border-bottom: 1px solid #e5e5e5;
  margin-bottom: 20px;
}

.ageSelector {
  display: flex;
  flex-direction: column;
}

/* Responsive Design */
@media (max-width: 1024px) {
    .search-property-container {
        flex-direction: column;
    }

    .search-property-window {
        width: 100%;
    }

    .advance-search-wrapper {
        flex-direction: column;
        gap: 8px;
    }

    .advance-search-option {
        border-right: none;
        border-bottom: 1px solid #e2e8f0;
    }

    .advance-search-option:last-child {
        border-bottom: none;
    }
}

@media (max-width: 768px) {
    .search-property-result-card {
        flex-direction: column;
    }

    .search-property-result-card--banner {
        width: 100%;
        height: 200px;
    }

    .search-property-result-card--price {
        width: 100%;
        border-left: none;
        border-top: 1px solid #e5e7eb;
        text-align: center;
    }

    .hotel-card--features-one,
    .hotel-card--features-three {
        flex-wrap: wrap;
        gap: 12px;
    }

    .hotel-card--features-one .icon {
        display: flex;
        align-items: center;
        justify-content: center;

    }

    .search-property-result--filter {
        flex-direction: column;
        align-items: flex-start;
        gap: 12px;
    }

    .search-property-result--filter-wrapper ul {
        flex-wrap: wrap;
        gap: 16px;
    }
}

@media (max-width: 480px) {
    .container {
        margin: 0;
    }

    .advance-search {
        padding: 16px;
    }

    .search-property-container {
        padding: 16px;
    }

    .search-property-window-container {
        padding: 16px;
    }

    .search-property-result-card--content,
    .search-property-result-card--price {
        padding: 16px;
    }

    .hotel-card--title {
        font-size: 16px;
    }

    .search-property-price--top span:last-of-type {
        font-size: 18px;
    }
}